@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
    font-family: "Poppins", sans-serif;
  box-sizing: border-box;
}

body {
  margin: 0;
  overflow: hidden;
}

.container {
  margin: 1rem;
  text-align: center;
}

.nav {
  position: fixed;
  top: 0;
  left: 50%; /* Position the navbar 50% from the left */
  transform: translateX(-50%); /* Center it by translating 50% of its width */
  width: 90%;
  max-width: 1800px; /* Ensure a maximum width to prevent excessive stretching */
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  gap: 2rem;
  padding: 1rem;
  z-index: 999; /* Keep it above other content */
}

.site-title {
  font-size: 2rem;
  font-weight: 900;
}

.nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  gap: 1rem;
}

.nav a {
  color: inherit;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 1rem;
}

.nav li.active {
  background-color: #0f0f0f;
}

.nav li:hover {
  background-color: #777;
}
